export default class AppSearch {
  constructor(urlConst, q, token) {
    this.urlConst = urlConst;
    this.q = q;
    this.nextPageToken = token;
  }

  async getQ() {
    const constInUrlSearch = `${this.urlConst.constInUrlSearch}q=${this.q}`;
    return constInUrlSearch;
  }

  async nextPageUrl() {
    const constInUrlSearch = `${this.urlConst.constInUrlSearch}q=${this.q}`;
    return `${constInUrlSearch}&pageToken=${this.nextPageToken}`;
  }

  async getVideos(videosId) {
    return `${this.urlConst.urlVideos}id=${videosId}&part=snippet,statistics`;
  }
}
