import SearchForm from '../view/SearchForm/SearchForm';
import App from './App';

export default function render() {
  const input = SearchForm();
  let inputTimeout;
  function finder(e) {
    if (!inputTimeout) {
      inputTimeout = setTimeout(() => {
        inputTimeout = null;
        const search = e.target.value;
        const app = new App(search);
        app.start();
      }, 1500);
    }
  }
  input.oninput = finder;
}
