import App from '../../controller/App';

export default class Carousel {
  constructor(settings, value, nextPageToken) {
    this.value = value;
    this.nextPageToken = nextPageToken;
    this.privates = {};
    this.privates.sel = {
      main: document.querySelector(settings.main),
      elem: document.querySelector(settings.elem),
      prev: document.querySelector(settings.prev),
      cur: document.querySelector(settings.cur),
      next: document.querySelector(settings.next),
    };
    this.slide = document.querySelector(settings.wrap);
  }

  create(firstEl) {
    this.numbFirstEl = firstEl || 1;
    this.qtyEl = this.slide.children.length;
    this.render(this.numbFirstEl);
    window.addEventListener('resize', () => {
      this.render(this.numbFirstEl);
    });
  }

  render() {
    const a = this.privates.sel.main;
    this.qtyElSlide = Math.floor(a.clientWidth / this.privates.sel.elem.clientWidth);
    // BUG: work uncorrect
    // this.slide.style.transform = `translateX(-${(this.numbFirstEl - 1) / this.qtyElSlide}00%)`;
    // next line - work correct
    this.slide.style.transform = `translateX(-${(this.numbFirstEl - 1) / this.qtyElSlide * 100}%)`;
    this.showNav();

    if (this.privates.sel.prev !== null) {
      if (this.numbFirstEl <= 1) {
        this.privates.sel.prev.style.visibility = 'hidden';
      }
      this.privates.sel.prev.onclick = () => {
        this.prevSlide();
      };
    }

    if (this.privates.sel.next !== null) {
      this.privates.sel.next.onclick = () => {
        this.nextSlide();
      };
    }

    // SWIPE
    this.slide.onselectstart = () => false;
    function unify(e) { return e.changedTouches ? e.changedTouches[0] : e; }
    let x0 = null;
    function lock(e) { x0 = unify(e).clientX; }
    const move = (e) => {
      if (x0 || x0 === 0) {
        const dx = unify(e).clientX - x0;
        const s = Math.sign(dx);
        if (s > 0 && this.privates.sel.prev.style.visibility === 'visible') {
          this.prevSlide();
        } else if (s < 0 && this.privates.sel.next.style.visibility === 'visible') {
          this.nextSlide();
        }
        x0 = null;
      }
    };

    this.slide.onmousedown = lock;
    this.privates.sel.main.ontouchstart = lock;
    this.slide.onmouseup = move;
    this.privates.sel.main.ontouchend = move;
  }

  prevSlide() {
    this.numbFirstEl -= this.qtyElSlide;
    this.showNav();
    this.slide.style.transform = `translateX(-${(this.numbFirstEl - 1) / this.qtyElSlide * 100}%)`;
    this.showNav();
  }

  nextSlide() {
    this.numbFirstEl += this.qtyElSlide;
    this.showNav();
    this.slide.style.transform = `translateX(-${(this.numbFirstEl - 1) / this.qtyElSlide * 100}%)`;
    if (this.numbFirstEl + this.qtyElSlide >= this.qtyEl) {
      const getClips = new App(this.value, this.nextPageToken);
      getClips.nextChunk(this.numbFirstEl);
    }
    this.showNav();
  }

  showNav() {
    this.privates.sel.prev.style.visibility = 'visible';
    this.privates.sel.next.style.visibility = 'visible';
    this.qtyElSlideShow = this.qtyEl - this.numbFirstEl + 1;
    if (this.qtyElSlide < 2) {
      this.privates.sel.cur.innerHTML = `${this.numbFirstEl}`;
    } else if (this.qtyElSlide > this.qtyElSlideShow && this.qtyElSlideShow < 2) {
      this.privates.sel.cur.innerHTML = `${this.numbFirstEl}`;
    } else if (this.qtyElSlide > this.qtyElSlideShow && this.qtyElSlideShow >= 2) {
      this.privates.sel.cur.innerHTML = `${this.numbFirstEl}...${this.numbFirstEl + this.qtyElSlideShow - 1}`;
    } else {
      this.privates.sel.cur.innerHTML = `${this.numbFirstEl}...${this.numbFirstEl + this.qtyElSlide - 1}`;
    }
    if (this.qtyElSlide > this.qtyElSlideShow) {
      this.privates.sel.next.style.visibility = 'hidden';
    }
    if (this.numbFirstEl <= 1) {
      this.numbFirstEl = 1;
      this.privates.sel.prev.style.visibility = 'hidden';
    } else {
      this.privates.sel.prev.style.visibility = 'visible';
    }
  }
}
