import AppModel from '../model/AppModel';
import AppSearch from '../model/AppSearch';
import AppView from '../view/AppView';
import Carousel from '../view/CarouselCr';

export default class App {
  constructor(value, nextPageToken) {
    this.state = {
      constInUrlSearch: 'https://www.googleapis.com/youtube/v3/search?key=AIzaSyDeenQMaBKQGf-h0dCbs5kKjZhIZiTn31M&type=video&part=snippet&maxResults=15&',
      urlVideos: 'https://www.googleapis.com/youtube/v3/videos?key=AIzaSyDeenQMaBKQGf-h0dCbs5kKjZhIZiTn31M&',
    };
    this.settings = {
      main: '.carousel',
      wrap: '.video',
      elem: '.video li',
      prev: '.prev',
      cur: '.cur',
      next: '.next',
    };
    this.value = value;
    this.nextPageToken = nextPageToken;
  }

  async start() {
    const req = new AppSearch(this.state, this.value);
    this.url = await req.getQ();
    const model = new AppModel(this.url);
    const searchVideoId = await model.getVideoId();
    if (searchVideoId) {
      const urlVideos = await req.getVideos(searchVideoId);
      this.clips = await model.getClips(urlVideos);
      this.nextPageToken = await model.getNextPageToken();
      const view = new AppView(this.clips);
      view.render();
      const carousel = new Carousel(this.settings, this.value, this.nextPageToken);
      carousel.create();
    } else {
      const view = new AppView();
      view.noRes();
    }
  }

  async nextChunk(numbFirstEl) {
    const req = new AppSearch(this.state, this.value, this.nextPageToken);
    this.url = await req.nextPageUrl();
    const model = new AppModel(this.url);
    const searchVideoId = await model.getVideoId();
    if (searchVideoId) {
      const urlVideos = await req.getVideos(searchVideoId);
      this.clips = await model.getClips(urlVideos);
      this.nextPageToken = await model.getNextPageToken();
      const view = new AppView(this.clips);
      view.renderNext();
      const carousel = new Carousel(this.settings, this.value, this.nextPageToken);
      carousel.create(numbFirstEl);
    }
  }
}
