export default class AppView {
  constructor(clips) {
    this.clips = clips;
  }

  clear() {
    this.wrapper = document.querySelector('.wrapper') || false;
    if (this.wrapper) {
      this.wrapper.remove();
    }
  }

  noRes() {
    this.clear();
    const wrapper = document.createElement('div');
    wrapper.className = 'wrapper no-res';
    wrapper.innerHTML = '<h2>No results found</h2><p>Search for other keywords, or CREATE YOUR VIDEO</p>';
    document.body.appendChild(wrapper);
  }

  render() {
    this.clear();
    const wrapper = document.createElement('div');
    wrapper.className = 'wrapper';
    const carousel = document.createElement('div');
    carousel.className = 'carousel';
    const butNav = document.createElement('div');
    const prev = document.createElement('button');
    const cur = document.createElement('p');
    const next = document.createElement('button');
    butNav.className = 'nav';
    prev.className = 'prev';
    cur.className = 'cur';
    next.className = 'next';
    butNav.appendChild(prev);
    butNav.appendChild(cur);
    butNav.appendChild(next);
    const content = document.createElement('ul');
    content.className = 'video';
    content.innerHTML = this.clips.map(clip => `<li>
    <h2>
    <a href="${clip.link}" target="_blank">
    ${clip.title}</a></h2>
    <img src="${clip.preview}
    ">
    <div class="info">
    <p class="author">${clip.author}</p>
    <p class="date">${clip.date}</p>
    <p class="views">${clip.viewCount.replace(/(\d)(?=(\d\d\d)+([^\d]|$))/g, '$1 ')}</p>
    <p class="description">${clip.description}</p></li></div>`).join('');
    wrapper.appendChild(carousel);
    carousel.appendChild(content);
    carousel.appendChild(butNav);
    document.body.appendChild(wrapper);
  }

  renderNext() {
    const content = document.querySelector('.video');
    content.innerHTML += this.clips.map(clip => `<li>
    <h2>
    <a href="${clip.link}" target="_blank">
    ${clip.title}</a></h2>
    <img src="${clip.preview}
    ">
    <div class="info">
    <p class="author">${clip.author}</p>
    <p class="date">${clip.date}</p>
    <p class="views">${clip.viewCount}</p>
    <p class="description">${clip.description}</p></li></div>`).join('');
  }
}
