export default class AppModel {
  constructor(q) {
    this.q = q;
  }

  async getVideoId() {
    this.dataSearch = await fetch(this.q).then(response => response.json());
    if (this.dataSearch.items && this.dataSearch.items.length < 1) {
      return false;
    }
    return this.dataSearch.items.map(clip => clip.id.videoId).join(',');
  }

  async getClips(urlVideos) {
    this.urlVideos = urlVideos;
    const videos = await fetch(this.urlVideos).then(response => response.json());
    return AppModel.extractCards(videos);
  }

  async getNextPageToken() {
    return this.dataSearch.nextPageToken ? this.dataSearch.nextPageToken : false;
  }

  static extractCards(videos) {
    return videos.items.map((clip) => {
      const obj = {};
      obj.link = `https://www.youtube.com/watch?v=${clip.id}`;
      obj.title = clip.snippet.title;
      obj.description = clip.snippet.description;
      obj.author = clip.snippet.channelTitle;
      if (clip.snippet.publishedAt) {
        obj.date = clip.snippet.publishedAt.slice(0, 10);
      }
      if (clip.snippet.thumbnails) {
        obj.preview = clip.snippet.thumbnails.medium.url;
      } else {
        obj.preview = 'http://www.sclance.com/pngs/no-image-png/no_image_png_935205.png';
      }
      obj.viewCount = clip.statistics.viewCount;
      return obj;
    });
  }
}
